.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;

  &__logo-wrapper {
    display: flex;
    align-items: center;
    gap: 20px;

    &:hover {
      cursor: pointer;
    }
  }

  &__logo {
    height: 70px;
    width: 70px;
    background-image: url('../../../public/images/logo.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__logo-title {
    font-weight: 700;
    font-size: 24px;
    margin: 0;
  }
}
